
import Vue from 'vue';
import {mapActions} from 'vuex';
import {Contract} from '@/interfaces';
import {PropType} from 'vue/types/options';
import {isValidWeb3Address} from '@/utils/common';

interface StoreMappedActions {
  grantRole(payload: { walletAddress: string, contract: Contract<any>, roleMethod: any }): Promise<void>;
}

interface Data {
  walletAddress: string;
  isLoading: boolean;
}

export default Vue.extend({
  props: {
    contract: {
      type: Object as PropType<Contract<any>>,
      required: true,
    },
    roleMethod: {
      type: Function as PropType<() => Promise<void>>,
      required: true,
    },
    roleName: {
      type: String as PropType<string>,
      required: true,
    },
  },

  data() {
    return {
      walletAddress: '',
      isLoading: false,
      isValidWeb3Address
    } as Data;
  },

  methods: {
    ...mapActions(['grantRole']) as StoreMappedActions,
    async onSubmit() {
      try {
        this.isLoading = true;
        await this.grantRole({walletAddress: this.walletAddress, contract: this.contract, roleMethod: this.roleMethod});
      } catch (e) {
        console.error(e);
      } finally {
        this.walletAddress = '';
        this.isLoading = false;
      }
    }
  },

});
